<template>
  <el-row style="margin-top: 20px;">
    <el-col :span="8" :offset="8">
      <el-card>
        <div slot="header" class="clearfix">
          <span style="font-weight: bold;color: #409EFF"><i class="el-icon-video-camera"></i>&nbsp;&nbsp;Lesson
            Demo:Bear Family</span>
        </div>
        <el-carousel :interval="2000" height="300px">
          <el-carousel-item v-for="(item,index) in pictrues" :key="index">
            <el-image :src="'/static/picture/'+item" fit="fill" />
          </el-carousel-item>
        </el-carousel>
        <div style="padding: 14px;">

          <div class="bottom clearfix">
            <el-row>
              <el-col :span="18">
                <div class="avatar">
                  <el-avatar :size="40" src="/static/images/teacher.jpg" style="display: inline-block;vertical-align: middle;margin-right: 10px;"></el-avatar>Mr
                  Mike
                  <el-tag type="success">8 minutes</el-tag>
                  <el-tag type="success">2343 studied</el-tag>
                </div>
                <div style="margin-top:10px ;">
                  <el-tag effect="dark" type="success">
                    Bear
                  </el-tag>
                  <el-tag effect="dark" type="info">
                    Baby bear
                  </el-tag>
                  <el-tag effect="dark" type="danger">
                    Mom bear
                  </el-tag>
                  <el-tag effect="dark" type="warning">
                    Bear family
                  </el-tag>
                </div>
              </el-col>
              <el-col :span="6">
                <div class="btn" style="margin-top: 20px;">
                  <el-button type="primary" round style="padding: 15px 15px;right: 0;" icon="el-icon-position" @click="gotoRouter">go
                    to study</el-button>
                </div>
              </el-col>

            </el-row>

          </div>

        </div>
      </el-card>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: 'entrance',
  data () {
    return {
      username: 'jack',
      title: 'lessons',
      loading: true,
      pictrues: ['Babypolarbear.jpg', 'Mompolarbear.jpg', 'Polarbearfamily.jpg', 'Babyblackbear.jpg',
        'Momblackbear.jpg', 'Blackbearfamily.jpg'
      ]

    }
  },
  mounted () {
    this.getData()
    document.title = this.$pjname + '-' + this.title
  },
  methods: {

    getData: function () {
      this.loading = false
    },
    gotoRouter: function () {
      this.$prompt('', "What's your name?", {
        inputValue: 'jack',
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel'
      }).then(({
        value
      }) => {
        this.$router.push({
          path: '/study/' + value
        })
      }).catch(() => {
        this.$message({
          type: 'error',
          message: 'user cancel'
        })
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  .time {
    font-size: 13px;
    color: #999;
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }

  .btn {
    float: right;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>



