<template>
  <div>
    <router-view class="web-body" :key="$router.fullPath"></router-view>
  </div>
</template>

<script>
export default {
  name: 'web',
  data: () => ({}),
  methods: {}
}
</script>

<style>
.web-header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2000;
}

.web-body {
  margin-top: 50px;
  min-height: 400px;
  overflow: hidden;
}

.container{
  width: 100%;
  max-width: 1200px;
  margin: 70px auto;
  display: block;
}
</style>



